import React, { PropTypes } from 'react';
import classnames from 'classnames';
import Select from '../../Select/Select';
import style from './style.css';
import Checkbox from '../../Checkbox/Checkbox';
import TextArea from '../../TextArea/TextArea';
import Shortcut from './Shortcut';
import * as preferenceEnums from '../../../constants/enums/preference';
import { reverseSchema } from '../../../utils/schema';
import ModalStyle from '../Modal.css';

const SendShortcutSelector = [
    { text: 'Press Enter to Send', value: preferenceEnums.shortcutSend.enter },
    { text: 'Press Ctrl+Enter to Send', value: preferenceEnums.shortcutSend.enterctrl },
];

const MacroSelector = [
  { text: 'Insert Macro', value: preferenceEnums.macros.insertMacro },
  { text: '{!Visitor.Name}', value: preferenceEnums.macros.visitorName },
  { text: '{!Visitor.Email}', value: preferenceEnums.macros.visitorEmail },
  { text: '{!Visitor.IP}', value: preferenceEnums.macros.visitorIp },
  { text: '{!Visitor.Country/Region}', value: preferenceEnums.macros.visitorCountry },
  { text: '{!Visitor.State/Province}', value: preferenceEnums.macros.visitorState },
  { text: '{!Visitor.City}', value: preferenceEnums.macros.visitorCity },
];

const enumMacroReverse = reverseSchema(preferenceEnums.macros);
const macroName = type => preferenceEnums.macrosText[enumMacroReverse[type]];

function insertMacro(selectedMacro, msg, insertStartPos, insertEndPos) {
  const macroText = selectedMacro === preferenceEnums.macros.insertMacro ?
    '' : macroName(selectedMacro);
  return msg.substring(0, insertStartPos).concat(macroText, msg.substring(insertEndPos));
}

class ChatTab extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedMacro: preferenceEnums.macros.insertMacro,
      insertStartPos: 0,
      insertEndPos: 0,
    };

    this.handleChangeSendShortcut = this.handleChangeSendShortcut.bind(this);
    this.handleChangeMacro = this.handleChangeMacro.bind(this);
    this.handlePreDefinedChange = this.handlePreDefinedChange.bind(this);
    this.handleChangeAutoSendMsg = this.handleChangeAutoSendMsg.bind(this);
    this.handleFocus = this.handleFocus.bind(this);
    this.handleChangeSCNextChat = this.handleChangeSCNextChat.bind(this);
    this.handleChangeSCNextResponse = this.handleChangeSCNextResponse.bind(this);
    this.handleChangeSCCannedMsg = this.handleChangeSCCannedMsg.bind(this);
  }

  handleChangeSendShortcut(value) {
    this.setState({
      shortcutSend: value,
    });
    const newInfo = Object.assign(this.props.info, { shortcutSend: value });
    this.props.updateChatTab(newInfo);
  }

  handleChangeAutoSendMsg(value) {
    const newInfo = Object.assign(this.props.info);
    newInfo.ifAutoSendMessage = value;
    this.props.updateChatTab(newInfo);
  }

  handleChangeSCNextChat(value) {
    const newInfo = Object.assign({}, this.props.info,
    { shortcutNextChat: value });
    this.props.updateChatTab(newInfo);
  }

  handleChangeSCNextResponse(value) {
    const newInfo = Object.assign({}, this.props.info,
    { shortcutNextResponse: value });
    this.props.updateChatTab(newInfo);
  }

  handleChangeSCCannedMsg(value) {
    const newInfo = Object.assign({}, this.props.info,
    { shortcutCannedMessage: value });
    this.props.updateChatTab(newInfo);
  }

  handleChangeMacro(value) {
    const newPos = this.state.insertStartPos + macroName(value).length;
    this.setState({
      insertStartPos: newPos,
      insertEndPos: newPos,
    });
    const newInfo = Object.assign({}, this.props.info);
    newInfo.messageWhenAcceptChat = insertMacro(value,
        newInfo.messageWhenAcceptChat, this.state.insertStartPos, this.state.insertEndPos);
    this.props.updateChatTab(newInfo);
  }

  handleFocus(event) {
    this.setState({
      insertStartPos: event.target.selectionStart,
      insertEndPos: event.target.selectionEnd,
    });
  }

  handlePreDefinedChange(event) {
    this.setState({
      insertStartPos: event.target.selectionStart,
      insertEndPos: event.target.selectionStart,
    });
    const newInfo = Object.assign(this.props.info);
    newInfo.messageWhenAcceptChat = event.target.value;
    this.props.updateChatTab(newInfo);
  }

  render() {
    return (
      <div className={style.settingsContent}>
        <div className={style.configCols}>
          <ul>
            <li>
              <div>{'Keyboard shortcut for \'Send\''}</div>
            </li>
            <li>
              <div>{'Keyboard shortcut for \'Next Chat\''}</div>
            </li>
            <li>
              <div>{'Keyboard shortcut for \'Next Response\''}</div>
            </li>
            <li>
              <div>{'Keyboard shortcut for \'Search Canned Message\''}</div>
            </li>
          </ul>
        </div>
        <div className={classnames(style.configCols, style.rightCols)}>
          <ul>
            <li>
              <div>
                <Select
                  input={{
                    value: this.props.info.shortcutSend,
                    onChange: this.handleChangeSendShortcut,
                  }}
                  options={SendShortcutSelector}
                  width={200}
                  dropdownClassName={ModalStyle.dropdown}
                />
              </div>
            </li>
            <li>
              <div>
                <Shortcut
                  keys={this.props.info.shortcutNextChat.length === 0 ?
                    [17, 69] : this.props.info.shortcutNextChat}
                  handleChange={this.handleChangeSCNextChat}
                />
              </div>
            </li>
            <li>
              <div>
                <Shortcut
                  keys={this.props.info.shortcutNextResponse.length === 0 ?
                    [17, 82] : this.props.info.shortcutNextResponse}
                  handleChange={this.handleChangeSCNextResponse}
                />
              </div>
            </li>
            <li>
              <div className={style.width40}>
                <Shortcut
                  keys={this.props.info.shortcutCannedMessage.length === 0 ?
                    [17, 70] : this.props.info.shortcutCannedMessage}
                  handleChange={this.handleChangeSCCannedMsg}
                />
              </div>
            </li>
          </ul>
        </div>
        <div className={classnames(style.configCols, style.fullWidth)}>
          <ul>
            <li>
              <div>
                <Checkbox
                  input={{
                    value: this.props.info.ifAutoSendMessage,
                    onChange: this.handleChangeAutoSendMsg }}
                  text="Automatically send a pre-defined message after a chat request is accepted"
                />
              </div>
              <div
                style={{ display: this.props.info.ifAutoSendMessage ? 'block' : 'none' }}
                className={style.preDefinedMessage}
              >
                <Select
                  input={{
                    value: this.state.selectedMacro,
                    onChange: this.handleChangeMacro,
                  }}
                  options={MacroSelector}
                  width={200}
                  dropdownClassName={ModalStyle.dropdown}
                />
                <br />
                <TextArea
                  meta={{}}
                  input={{
                    value: this.props.info.messageWhenAcceptChat,
                    onChange: this.handlePreDefinedChange,
                    onClick: this.handleFocus,
                  }}
                  name="preDefinedMessage"
                  rows="4"
                />
              </div>
            </li>
          </ul>
        </div>
      </div>
    );
  }
}

export default ChatTab;

ChatTab.propTypes = {
  info: PropTypes.shape({
    shortcutSend: PropTypes.number.isRequired,
    shortcutCannedMessage: React.PropTypes.arrayOf(React.PropTypes.number).isRequired,
    shortcutNextChat: React.PropTypes.arrayOf(React.PropTypes.number).isRequired,
    shortcutNextResponse: React.PropTypes.arrayOf(React.PropTypes.number).isRequired,
    ifAutoSendMessage: PropTypes.bool.isRequired,
    messageWhenAcceptChat: PropTypes.string.isRequired,
  }).isRequired,
  updateChatTab: React.PropTypes.func.isRequired,
};



