import React from 'react';
import classnames from 'classnames';
import ReactTooltip from 'react-tooltip';
// import Tooltip from '../../Tooltip/Tooltip';
import style from './style.css';
import { icons } from '../../../constants/enums';
import Icon from '../../Icon/Icon';
import iconStyle from '../../Icons/Icon.css';
import commonStyle from '../../CommonResources/common.css';

class Sound extends React.Component {
  constructor(props) {
    super(props);

    this.audioSource = null;
    this.clickPlay = this.clickPlay.bind(this);
    this.clickMute = this.clickMute.bind(this);
    this.setVolume = this.setVolume.bind(this);
  }

  componentDidMount() {
    this.volumeInput.addEventListener('change', () => {
      this.setVolume(event);
    });
  }

  setVolume(event) {
    const volume = parseInt(event.target.value, 10);
    let ifMute = this.props.mute;
    ifMute = volume === 0;
    this.props.changeHandle({
      vol: volume,
      mute: ifMute,
    });
  }

  clickPlay() {
    if (this.props.mute) {
      return;
    }
    this.audioSource.volume = this.props.volume / 100;
    this.audioSource.load();
    this.audioSource.play();
  }

  clickMute() {
    this.props.changeHandle({
      vol: this.props.volume,
      mute: !this.props.mute,
    });
  }

  render() {
    return (
      <div className={style.configCols}>
        <audio
          ref={(resource) => { this.audioSource = resource; }}
        >
          <source
            src={this.props.url}
            type={'audio/mpeg'}
          />
        </audio>
        <div className={classnames(style.soundItem, style.soundIconBtn)}>
          <Icon
            style={{
              display: this.props.mute ? 'block' : 'none',
            }}
            type={icons.soundPlay}
            className={iconStyle.soundMute}
            size={20}
            onClick={this.clickMute}
          />
          <Icon
            style={{
              display: this.props.volume <= 50 && !this.props.mute ? 'block' : 'none',
            }}
            type={icons.soundPlay}
            className={iconStyle.soundVolumeLow}
            size={20}
            onClick={this.clickMute}
          />
          <Icon
            style={{
              display: this.props.volume > 50 && !this.props.mute ? 'block' : 'none',
            }}
            type={icons.soundPlay}
            className={iconStyle.soundVolumeHigh}
            size={20}
            onClick={this.clickMute}
          />
        </div>
        <div
          className={classnames(
            style.soundItem,
            this.props.mute && style.disable,
          )}
        >
          <input
            type="range"
            min="0"
            max="100"
            step="1"
            value={this.props.volume}
            onChange={this.setVolume}
            style={{ width: 120 }}
            ref={(element) => { this.volumeInput = element; }}
          />
        </div>
        <div className={classnames(style.soundItem, style.soundIconBtn)}>
          <Icon
            type={icons.soundMute}
            className={this.props.mute ? iconStyle.soundPlayDisable : iconStyle.soundPlay}
            style={{ marginLeft: 20 }}
            size={16}
            disabled={this.props.mute}
            onClick={this.clickPlay}
            data-tip={'Play'}
            data-for="sound"
          />
          <ReactTooltip
            id="sound"
            effect="solid"
            place="bottom"
            class={commonStyle.tooltipInfo}
          />
        </div>
      </div>
    );
  }
}

export default Sound;

Sound.propTypes = {
  url: React.PropTypes.string.isRequired,
  volume: React.PropTypes.number.isRequired,
  mute: React.PropTypes.bool.isRequired,
  changeHandle: React.PropTypes.func.isRequired,
};



