import React from 'react';
import classnames from 'classnames';
import style from './style.css';
import Sound from './Sound';
import Switch from '../../Switch/Switch';
import * as preferenceEnums from '../../../constants/enums/preference';
import Select from '../../Select/Select';
import { reverseSchema } from '../../../utils/schema';
import ModalStyle from '../Modal.css';

const soundList = (soundArray) => {
  const arr = [];
  soundArray.map((item) => {
    arr.push({
      text: item.name.split('.')[0],
      value: item.id,
    });
    return item;
  });
  return arr;
};

const enumSoundTypeReverse = reverseSchema(preferenceEnums.soundType);
const soundTypeName = type => preferenceEnums.soundTypeName[enumSoundTypeReverse[type]];

class SoundItem extends React.Component {
  constructor(props) {
    super(props);

    this.handleSoundIdChange = this.handleSoundIdChange.bind(this);
    this.handleSoundChange = this.handleSoundChange.bind(this);
    this.handlePopupChange = this.handlePopupChange.bind(this);
  }

  handleSoundIdChange(value) {
    this.props.onChange(Object.assign(this.props.soundInfo, {
      id: value,
    }));
  }

  handleSoundChange({ vol, mute }) {
    this.props.onChange(Object.assign(this.props.soundInfo, {
      volume: vol,
      ifSoundOn: !mute,
    }));
  }

  handlePopupChange(value) {
    this.props.onChange(Object.assign(this.props.soundInfo, {
      ifPopup: value,
    }));
  }

  render() {
    return (
      <tr>
        <td className={style.soundTitle}>{soundTypeName(this.props.soundInfo.type)}</td>
        <td className={style.soundConfig}>
          <div
            className={classnames(style.soundSelector, style.configCols)}
          >
            <Select
              options={soundList(this.props.systemSounds)}
              width={98}
              input={{
                value: this.props.soundInfo.id,
                onChange: this.handleSoundIdChange,
              }}
              dropdownClassName={ModalStyle.dropdown}
              customizeStyle={{ border: 'none', background: 'transparent' }}
            />
          </div>
          <Sound
            url={`${this.props.soundUrl}?soundId=${this.props.soundInfo.id}`}
            volume={this.props.soundInfo.volume}
            mute={!this.props.soundInfo.ifSoundOn}
            changeHandle={this.handleSoundChange}
          />
        </td>
        {
          this.props.ifSupportNotification &&
          <td>
            <Switch
              checked={this.props.soundInfo.ifPopup}
              handleChange={this.handlePopupChange}
              id={this.props.soundInfo.type.toString()}
            />
          </td>
        }
      </tr>
    );
  }
}

export default SoundItem;

SoundItem.propTypes = {
  soundInfo: React.PropTypes.arrayOf(React.PropTypes.shape({
    type: React.PropTypes.number.isRequired,
    id: React.PropTypes.number.isRequired,
    volume: React.PropTypes.number.isRequired,
    ifSoundOn: React.PropTypes.bool.isRequired,
    ifPopup: React.PropTypes.bool.isRequired,
  })).isRequired,
  systemSounds: React.PropTypes.arrayOf(React.PropTypes.shape({
    id: React.PropTypes.number.isRequired,
    name: React.PropTypes.string.isRequired,
  })),
  soundUrl: React.PropTypes.string.isRequired,
  onChange: React.PropTypes.func,
  ifSupportNotification: React.PropTypes.bool,
};



