import * as _ from 'lodash';
import { connect } from 'react-redux';
import Settings from '../components/Modal/Settings/Settings';
import * as preferenceEnums from '../constants/enums/preference';
import * as selectors from '../reducers/selectors';
import { hideSettingsModal, showSettingsModal, testG2MConnection } from '../actions/ui';
import { setPreference, updatePreference, checkG2MAccount, connectG2MAccount, agentMonitorAllChats } from '../actions/agent';
import * as common from './Business/common';
import * as localStorage from '../utils/localStorage';
import * as localStorageKeys from '../constants/localStorage';
import { ipcRenderer } from '../services/electronAPI';
import * as channelsType from '../constants/enums/channels';

const Column = (col, visible, columnWidth, variableId = 0) => ({
  enumColumn: col,
  ifVisible: visible,
  width: columnWidth,
  customVariableId: variableId,
});

const ifValidCustomVariable = (variableId, variables) =>
  variables.filter(item => item.id === variableId).length > 0;

const ifNewCustomVariable = (variableId, columns) =>
  columns.filter(item => item.customVariableId === variableId).length === 0;

const getValidColumns = (columns, variables, siteInfo) => {
  const validColumns = [];
  let segmentColumn;
  let ticketColumn;
  let departmentColumn;
  let autoInvitationColumn;
  let campaignColumn;
  /** @todo consider using selectors.getVisibleVisitorListColumns */
  _.each(columns, (soureItem) => {
    const item = Object.assign({}, soureItem);
    switch (item.enumColumn) {
      case preferenceEnums.enumColumn.CustomVariable:
        if (ifValidCustomVariable(item.customVariableId, variables)) {
          validColumns.push(item);
        }
        break;
      case preferenceEnums.enumColumn.Segment:
        if (siteInfo.ifEnableSegmentation) {
          segmentColumn = item;
          validColumns.push(item);
        }
        break;
      case preferenceEnums.enumColumn.Department:
        if (siteInfo.ifDepartmentEnable) {
          departmentColumn = item;
          validColumns.push(item);
        }
        break;
      case preferenceEnums.enumColumn.TicketId:
        if (siteInfo.ifTicketEnable) {
          ticketColumn = item;
          validColumns.push(item);
        }
        break;
      case preferenceEnums.enumColumn.AutoInvitation:
        if (siteInfo.ifCanCustomAutoInvitationRules) {
          autoInvitationColumn = item;
          validColumns.push(item);
        }
        break;
      case preferenceEnums.enumColumn.WebsiteIntegrationPlan:
        if (siteInfo.ifMultipleCodePlan) {
          campaignColumn = item;
          validColumns.push(item);
        }
        break;
      default:
        validColumns.push(item);
        break;
    }
  });
  if (!segmentColumn && siteInfo.ifEnableSegmentation) {
    validColumns.push(new Column(preferenceEnums.enumColumn.Segment, true, 120));
  }
  if (!departmentColumn && siteInfo.ifDepartmentEnable) {
    validColumns.push(new Column(preferenceEnums.enumColumn.Department, false, 100));
  }
  if (!ticketColumn && siteInfo.ifTicketEnable) {
    validColumns.push(new Column(preferenceEnums.enumColumn.TicketId, false, 100));
  }
  if (!autoInvitationColumn && siteInfo.ifCanCustomAutoInvitationRules) {
    validColumns.push(new Column(preferenceEnums.enumColumn.AutoInvitation, false, 200));
  }
  if (!campaignColumn && siteInfo.ifMultipleCodePlan) {
    validColumns.push(new Column(preferenceEnums.enumColumn.WebsiteIntegrationPlan, false, 200));
  }
  _.each(variables, (item) => {
    if (ifNewCustomVariable(item.id, columns)) {
      validColumns.push(new Column(preferenceEnums.enumColumn.CustomVariable, false, 100, item.id));
    }
  });
  return validColumns;
};

const mapStateToProps = (state) => {
  const ifCanNotification = selectors.getIfSupportNotification();
  return {
    ifOpen: state.ui.preference.ifOpen,
    selectedTab: state.ui.preference.selectedTab,
    isTesting: state.ui.preference.isTesting,
    ifTestG2ASuccess: state.ui.preference.ifTestG2ASuccess,
    errorMsg: state.ui.preference.errorMsg,
    ifSupportNotification: ifCanNotification,
    tabs: [
      { name: 'Chat', type: preferenceEnums.preferenceTabs.chat },
      { name: 'Language', type: preferenceEnums.preferenceTabs.language },
      {
        name: ifCanNotification ? 'Sound & Popup' : 'Sound',
        type: preferenceEnums.preferenceTabs.soundpopup,
      },
      { name: 'Columns', type: preferenceEnums.preferenceTabs.columns },
      { name: 'Others', type: preferenceEnums.preferenceTabs.others },
    ],
    siteInfo: {
      soundUrl: common.getSoundUrl(state),
      ifEnableChatIntegration: state.config.settings.siteInfo && state.config.settings.siteInfo.ifEnableChatIntegration,
      ifEnableChatTranslation: state.config.settings.siteInfo && state.config.settings.siteInfo.ifEnableChatTranslation,
      ifAutoAcceptChat: state.config.settings.siteInfo && state.config.settings.siteInfo.ifAutoAcceptChat,
      ifEnableAutoAllocation: state.config.settings.siteInfo && state.config.settings.siteInfo.ifEnableAutoAllocation,
      ifEnableTeamWork: state.config.settings.siteInfo && state.config.settings.siteInfo.ifEnableTeamWork,
      ifEnableSegmentation: state.config.settings.siteInfo && state.config.settings.siteInfo.ifEnableSegmentation,
      ifEnableScreenSharing: state.config.settings.siteInfo && state.config.settings.siteInfo.ifEnableScreenSharing,
      ifEnableGotoMeeting: state.config.settings.siteInfo && state.config.settings.siteInfo.ifEnableGotoMeeting,
      ifEnableJoinMe: state.config.settings.siteInfo && state.config.settings.siteInfo.ifEnableJoinMeScreenSharing,
      ifTicketEnable: state.config.settings.siteInfo && state.config.settings.siteInfo.ifTicketOpen,
      ifDepartmentEnable: state.config.settings.siteInfo && state.config.settings.siteInfo.ifDepartmentEnabled,
      ifMultipleCodePlan: state.config.settings.siteInfo && state.config.settings.siteInfo.ifEnableMultipleCodePlan,
      ifCanCustomAutoInvitationRules: state.config.settings.siteInfo && state.config.settings.siteInfo.ifCanCustomAutoInvitationRules,
      ifRecordNavigation: state.config.settings.siteInfo && state.config.settings.siteInfo.ifRecordNavigation,
      systemSounds: state.config.settings.sound,
      customVariables: state.config.settings.customVariables,
      ifUseMainServer: state.config.ifUseMainServer,
      ifEnableAudioVideoChat: state.config.settings.siteInfo
      && state.config.settings.siteInfo.ifEnableAudioVideoChat,
    },
    chatInfo: {
      shortcutSend: state.config.preference.shortcutSend,
      shortcutCannedMessage: state.config.preference.others.shortcutCannedMessage,
      shortcutNextChat: state.config.preference.others.shortcutNextChat,
      shortcutNextResponse: state.config.preference.others.shortcutNextResponse,
      ifAutoSendMessage: state.config.preference.ifAutoSendMessage,
      messageWhenAcceptChat: state.config.preference.messageWhenAcceptChat,
    },
    languageInfo: {
      textDirectionIfRTL: state.config.preference.textDirectionIfRTL,
      ifEnableSpellCheck: state.config.preference.ifEnableSpellCheck,
      languageSpellCheck: state.config.preference.languageSpellCheck,
      ifEnableTranslate: state.config.preference.ifEnableTranslate,
      translateLanguage: state.config.preference.translateLanguage,
    },
    soundpopupInfo: Object.assign(_.pick(state.config.preference, [
      'visitorEnterSiteSoundId',
      'visitorEnterSiteSoundVolume',
      'ifVisitorEnterSitePopup',
      'ifVisitorEnterSiteSoundOn',
      'newChatRequestSoundId',
      'newChatRequestSoundVolume',
      'ifNewChatRequestPopup',
      'ifNewChatRequestSoundOn',
      'chatInQueueSoundId',
      'chatInQueueSoundVolume',
      'ifChatInQueuePopup',
      'ifChatInQueueSoundOn',
      'newResponseSoundId',
      'newResponseSoundVolume',
      'ifNewResponsePopup',
      'ifNewResponseSoundOn',
      'chatEndedSoundId',
      'chatEndedSoundVolume',
      'ifChatEndedPopup',
      'ifChatEndedSoundOn',
      'transferSoundId',
      'transferSoundVolume',
      'ifTransferPopup',
      'ifTransferSoundOn',
      'privateMessageSoundId',
      'privateMessageSoundVolume',
      'ifPrivateMessageSoundOn',
      'ifPrivateMessagePopup',
      'exceptionSoundId',
      'exceptionSoundVolume',
      'ifExceptionPopup',
      'ifExceptionSoundOn',

    ]), _.pick(state.config.preference.others, [
      'segmentationSoundId',
      'segmentationSoundVolume',
      'ifSegmentationPopup',
      'ifSegmentationSoundOn',
      'audioVideoChatRequestSoundId',
      'audioVideoChatRequestSoundVolume',
      'ifAudioVideoChatRequestPopup',
      'ifAudioVideoChatRequestSoundOn',
      'audioVideoChatEndSoundId',
      'audioVideoChatEndSoundVolume',
      'ifAudioVideoChatEndPopup',
      'ifAudioVideoChatEndSoundOn',
    ])),
    columnsInfo: getValidColumns(state.config.preference.visitorMonitorColumns,
      state.config.settings.customVariables,
      {
        ifTicketEnable: state.config.settings.siteInfo && state.config.settings.siteInfo.ifTicketOpen,
        ifDepartmentEnable: state.config.settings.siteInfo && state.config.settings.siteInfo.ifDepartmentEnabled,
        ifMultipleCodePlan: state.config.settings.siteInfo && state.config.settings.siteInfo.ifEnableMultipleCodePlan,
        ifCanCustomAutoInvitationRules:
        state.config.settings.siteInfo && state.config.settings.siteInfo.ifCanCustomAutoInvitationRules,
        ifEnableSegmentation: state.config.settings.siteInfo && state.config.settings.siteInfo.ifEnableSegmentation,
      }),
    othersInfo: {
      ifAutoStartup: localStorage.get(localStorageKeys.ifAutoRun, true) === 'true',
      ifAutoLogin: localStorage.getBool(localStorageKeys.rememberMe),
      ifExitCloseWindow: localStorage.get(localStorageKeys.ifExitCloseWindow, true) === 'true',
      ifEnableAutoMonitor: state.config.preference.others.ifEnableAutoMonitor,
      ifMonitorChatPermission: selectors.ifHavePermissionMonitorChat(state),
      autoCloseChatDelayMinutes: state.config.preference.others.autoCloseChatDelayMinutes,
      autoCloseChatWindow: state.config.preference.others.autoCloseChatWindow,
      g2mEmail: state.config.preference.g2mEmail,
      g2mPassword: state.config.preference.g2mPassword,
      ifG2MConnected: state.config.preference.ifG2MConnected,
      joinMeIntegrationServer: selectors.getJoinMeIntegrationServer(state),
      agentInfo: {
        siteId: selectors.getSiteId(state),
        agentId: state.agent.id,
        token: state.agent.session,
      },
      ifAutoAway: state.config.preference.others.ifAutoAway,
      autoAwayMinutes: state.config.preference.others.autoAwayMinutes,
      ifAutoLogoff: state.config.preference.others.ifAutoLogoff,
      autoLogoffMinutes: state.config.preference.others.autoLogoffMinutes,
    },
  };
};

const mergeProps = (stateProps, dispatchProps) => {
  const { dispatch } = dispatchProps;

  const dispatchObject = {
    closeModal() {
      dispatch(hideSettingsModal());
    },
    clickTab(item) {
      dispatch(showSettingsModal(item));
    },
    saveSettings(preference, siteInfo) {
      const enableAutoMonitor = !stateProps.othersInfo.ifEnableAutoMonitor &&
        preference.othersInfo.ifEnableAutoMonitor;
      dispatch(updatePreference(preference));
      dispatch(setPreference());
      if (enableAutoMonitor) {
        dispatch(agentMonitorAllChats());
      }
      if (siteInfo.ifEnableScreenSharing && siteInfo.ifEnableGotoMeeting) {
        dispatch(connectG2MAccount(preference.othersInfo.g2mEmail,
          preference.othersInfo.g2mPassword));
      }
      localStorage.set(localStorageKeys.rememberMe, preference.othersInfo.ifAutoLogin);
      if (!process.env.IS_WEB_APP) {
        localStorage.set(localStorageKeys.ifAutoRun, preference.othersInfo.ifAutoStartup);
        localStorage.set(localStorageKeys.ifExitCloseWindow,
          preference.othersInfo.ifExitCloseWindow);
        ipcRenderer.send(channelsType.preferanceSettings, {
          autoLogin: preference.othersInfo.ifAutoLogin,
          autoStartup: preference.othersInfo.ifAutoStartup,
          autoAwayMinutes: preference.othersInfo.autoAwayMinutes,
          autoLogoffMinutes: preference.othersInfo.autoLogoffMinutes,
        });
      }
    },
    testG2MConnect(email, password) {
      dispatch(testG2MConnection());
      dispatch(checkG2MAccount(email, password));
    },
  };
  return {
    ...stateProps,
    ...dispatchObject,
  };
};

// export default connect(mapStateToProps, mapDispatchToProps, mergeProps)(Settings);
export default connect(mapStateToProps, null, mergeProps)(Settings);



