import React from 'react';
import classnames from 'classnames';
import style from './style.css';
import SoundItem from './SoundItem';

class SoundPopupTab extends React.Component {
  constructor(props) {
    super(props);
    this.handleChange = this.handleChange.bind(this);
  }

  handleChange(item) {
    this.props.updateSoundTab(item);
  }

  render() {
    return (
      <div className={classnames(style.settingsContent, style.soundSettings)}>
        <table>
          <thead>
            <tr>
              <th>Scenario</th>
              <th>Alert Sound</th>
              { this.props.ifSupportNotification && <th>Alert Popup</th> }
            </tr>
          </thead>
          <tbody>
            {
              this.props.info.map((item, i) => (
                <SoundItem
                  key={`item_${i}`}
                  soundInfo={item}
                  systemSounds={this.props.siteInfo.systemSounds}
                  soundUrl={this.props.siteInfo.soundUrl}
                  onChange={this.handleChange}
                  ifSupportNotification={this.props.ifSupportNotification}
                />
              ))
            }
          </tbody>
        </table>
      </div>
    );
  }
}

export default SoundPopupTab;

SoundPopupTab.propTypes = {
  info: React.PropTypes.arrayOf(React.PropTypes.shape({
    type: React.PropTypes.number.isRequired,
    id: React.PropTypes.number.isRequired,
    volume: React.PropTypes.number.isRequired,
    ifSoundOn: React.PropTypes.bool.isRequired,
    ifPopup: React.PropTypes.bool.isRequired,
  })).isRequired,
  siteInfo: React.PropTypes.shape({
    soundUrl: React.PropTypes.string.isRequired,
    ifAutoAcceptChat: React.PropTypes.bool.isRequired,
    ifEnableAutoAllocation: React.PropTypes.bool.isRequired,
    ifEnableTeamWork: React.PropTypes.bool.isRequired,
    ifEnableSegmentation: React.PropTypes.bool.isRequired,
    systemSounds: React.PropTypes.arrayOf(React.PropTypes.shape({
      id: React.PropTypes.number.isRequired,
      name: React.PropTypes.string.isRequired,
    })),
  }),
  updateSoundTab: React.PropTypes.func.isRequired,
  ifSupportNotification: React.PropTypes.bool,
};



