import axios from 'axios';

const getUrl = (server, action, siteId, agentId, token) =>
  `${server}/accountService.ashx?action=${action}&siteId=${siteId}&operatorId=${agentId}&token=${token}`;

export const resetJoinMe = (server, { siteId, agentId, token }) =>
  new Promise((resolve, reject) => {
    const url = getUrl(server, 'ResetAccount', siteId, agentId, token);
    axios.get(url)
      .then(() => resolve())
      .catch(err => reject(err));
  });

export const joinMeConnect = (server, { siteId, agentId, token }) =>
  new Promise((resolve, reject) => {
    const url = getUrl(server, 'GetAccountName', siteId, agentId, token);
    axios.get(url)
      .then((resp) => {
        resolve({
          connected: resp.data.errorCode === 0,
          accountName: resp.data.accountName,
        });
      })
      .catch(err => reject(err));
  });



