import React from 'react';
import classnames from 'classnames';
import * as reporter from 'src/utils/reporter';
import style from './style.css';
import Checkbox from '../../Checkbox/Checkbox';
import Input from '../../Input/Input';
import inputStyle from '../../Input/Input.css';
import Icon from '../../Icon/Icon';
import iconStyle from '../../Icons/Icon.css';
import { icons } from '../../../constants/enums';
import { resetJoinMe, joinMeConnect } from '../../../services/joinme';
import Select from '../../Select/Select';
import ModalStyle from '../Modal.css';

const CloseActionSelect = [
  { text: 'Exit', value: 0 },
  { text: 'Minimize window to system tray', value: 1 },
];

class OthersTab extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      ifJoinMeConnnected: false,
      joinMeAccount: '',
      checkingJoinMe: true,
      ifWeb: process.env.IS_WEB_APP,
    };

    this.handleAutoRun = this.handleAutoRun.bind(this);
    this.handleAutoLogin = this.handleAutoLogin.bind(this);
    this.handleAutoAway = this.handleAutoAway.bind(this);
    this.handleAutoAwayMinutes = this.handleAutoAwayMinutes.bind(this);
    this.handleAutoLogoff = this.handleAutoLogoff.bind(this);
    this.handleLogoffMinutes = this.handleLogoffMinutes.bind(this);
    this.handleAutoMonitor = this.handleAutoMonitor.bind(this);
    this.handleAutoCloseChatWindow = this.handleAutoCloseChatWindow.bind(this);
    this.handleCloseChatDelayMinutes = this.handleCloseChatDelayMinutes.bind(this);
    this.handleChangeG2MEmail = this.handleChangeG2MEmail.bind(this);
    this.handleChangeG2MPassword = this.handleChangeG2MPassword.bind(this);
    this.handleChangeClosingAction = this.handleChangeClosingAction.bind(this);
    this.clickG2MConnection = this.clickG2MConnection.bind(this);
    this.clickResetG2M = this.clickResetG2M.bind(this);
    this.clickResetJoinMe = this.clickResetJoinMe.bind(this);
  }

  componentDidMount() {
    if (this.props.focusElement === 'autoCloseChatDelayMinutes') {
      this.autoCloseChatDelayMinutes.focus();
    } else if (this.props.focusElement === 'autoAwayMinutes') {
      this.autoAwayMinutes.focus();
    } else if (this.props.focusElement === 'autoLogoffMinutes') {
      this.autoLogoffMinutes.focus();
    }
    if (this.props.ifEnableJoinMe) {
      joinMeConnect(this.props.info.joinMeIntegrationServer, this.props.info.agentInfo)
        .then(({ connected, accountName }) => {
          if (connected) {
            this.setState({
              ifJoinMeConnnected: connected,
              joinMeAccount: accountName,
              checkingJoinMe: false,
            });
          } else {
            this.setState({
              ifJoinMeConnnected: connected,
              joinMeAccount: '',
              checkingJoinMe: false,
            });
          }
        })
        .catch((error) => {
          reporter.error(error, 'OthersTab', 73, 0);
        });
    }
  }

  handleAutoMonitor(value) {
    this.props.updateOthersTab(Object.assign({}, this.props.info, { ifEnableAutoMonitor: value }));
  }

  handleAutoRun(value) {
    this.props.updateOthersTab(Object.assign({}, this.props.info, { ifAutoStartup: value }));
  }

  handleAutoLogin(value) {
    this.props.updateOthersTab(Object.assign({}, this.props.info, { ifAutoLogin: value }));
  }

  handleAutoAway(value) {
    this.props.updateOthersTab(Object.assign({}, this.props.info, { ifAutoAway: value }));
  }

  handleAutoAwayMinutes(e) {
    this.props.updateOthersTab(Object.assign({}, this.props.info,
      { autoAwayMinutes: e.target.value }));
  }

  handleAutoLogoff(value) {
    this.props.updateOthersTab(Object.assign({}, this.props.info, { ifAutoLogoff: value }));
  }

  handleLogoffMinutes(e) {
    this.props.updateOthersTab(Object.assign({}, this.props.info,
      { autoLogoffMinutes: e.target.value }));
  }

  handleAutoCloseChatWindow(value) {
    this.props.updateOthersTab(Object.assign({}, this.props.info,
      { autoCloseChatWindow: value }));
  }

  handleCloseChatDelayMinutes(e) {
    this.props.updateOthersTab(Object.assign({}, this.props.info,
      { autoCloseChatDelayMinutes: e.target.value }));
  }

  handleChangeG2MEmail(e) {
    this.props.updateOthersTab(Object.assign({}, this.props.info,
      { g2mEmail: e.target.value }));
  }

  handleChangeG2MPassword(e) {
    this.props.updateOthersTab(Object.assign({}, this.props.info,
      { g2mPassword: e.target.value }));
  }

  handleChangeClosingAction(value) {
    this.props.updateOthersTab(Object.assign({}, this.props.info,
      { ifExitCloseWindow: value === 0 }));
  }

  clickG2MConnection() {
    this.props.testG2MConnect(this.props.info.g2mEmail, this.props.info.g2mPassword);
  }

  clickResetG2M() {
    const newInfo = Object.assign({}, this.props.info, {
      g2mEmail: '',
      g2mPassword: '',
      ifG2MConnected: false,
    });
    this.props.updateOthersTab(newInfo);
  }

  clickResetJoinMe() {
    resetJoinMe(this.props.info.joinMeIntegrationServer, this.props.info.agentInfo)
        .then(() => {
          this.setState({
            ifJoinMeConnnected: false,
            joinMeAccount: '',
            checkingJoinMe: false,
          });
        })
        .catch((error) => {
          reporter.error(error, 'OthersTab', 156, 0);
        });
  }

  render() {
    return (
      <div className={style.settingsContent}>
        <ul>
          {
            !process.env.IS_WEB_APP
            &&
            <li>
              <div>
                <Checkbox
                  input={{
                    value: this.props.info.ifAutoStartup,
                    onChange: this.handleAutoRun,
                  }}
                  text="Start Agent Console as your machine starts up."
                />
              </div>
            </li>
          }
          {
            this.props.info.ifMonitorChatPermission
            &&
            <li>
              <div>
                <Checkbox
                  input={{
                    value: this.props.info.ifEnableAutoMonitor,
                    onChange: this.handleAutoMonitor }}
                  text="Automatically monitor all ongoing and new incoming chats."
                />
              </div>
            </li>
          }
          <li>
            <div>
              <Checkbox
                input={{
                  value: this.props.info.autoCloseChatWindow,
                  onChange: this.handleAutoCloseChatWindow }}
                text="Automatically close the chat window "
              />
              <Input
                meta={{}}
                type="text"
                className={classnames(inputStyle.input, style.shortInput)}
                input={{
                  value: this.props.info.autoCloseChatDelayMinutes,
                  onChange: this.handleCloseChatDelayMinutes,
                }}
                inputRef={(input) => { this.autoCloseChatDelayMinutes = input; }}
              />
              <span>{'minutes after a chat session ends.'}</span>
            </div>
          </li>
          {
            !process.env.IS_WEB_APP
            &&
            <li>
              <div>
                <Checkbox
                  input={{
                    value: this.props.info.ifAutoAway,
                    onChange: this.handleAutoAway }}
                  text="Auto away: Automatically switch to Away after"
                />
                <Input
                  meta={{}}
                  type="text"
                  className={classnames(inputStyle.input, style.shortInput)}
                  input={{
                    value: this.props.info.autoAwayMinutes,
                    onChange: this.handleAutoAwayMinutes,
                  }}
                  inputRef={(input) => { this.autoAwayMinutes = input; }}
                />
                <span>{'minutes of no mouse or keyboard input.'}</span>
              </div>
            </li>
          }
          {
            !process.env.IS_WEB_APP
            &&
            <li>
              <div>
                <Checkbox
                  input={{
                    value: this.props.info.ifAutoLogoff,
                    onChange: this.handleAutoLogoff }}
                  text="Auto Logout: Automatically logout out after"
                />
                <Input
                  meta={{}}
                  type="text"
                  className={classnames(inputStyle.input, style.shortInput)}
                  input={{
                    value: this.props.info.autoLogoffMinutes,
                    onChange: this.handleLogoffMinutes,
                  }}
                  inputRef={(input) => { this.autoLogoffMinutes = input; }}
                />
                <span>{'minutes of no mouse or keyboard input.'}</span>
              </div>
            </li>
          }
          <li>
            <div>
              <Checkbox
                input={{
                  value: this.props.info.ifAutoLogin,
                  onChange: this.handleAutoLogin }}
                text="Automatically login with last login information."
              />
            </div>
          </li>
          {
            !process.env.IS_WEB_APP
            &&
            <li>
              <div>
                <span style={{ paddingLeft: 20 }}>
                  {'What action to take when closing Agent Console: '}
                </span>
                <div style={{ paddingLeft: 10, display: 'inline-block' }}>
                  <Select
                    input={{
                      value: this.props.info.ifExitCloseWindow ? 0 : 1,
                      onChange: this.handleChangeClosingAction,
                    }}
                    options={CloseActionSelect}
                    width={240}
                    dropdownClassName={ModalStyle.dropdown}
                  />
                </div>
              </div>
            </li>
          }
          {
            this.props.ifEnableScreenSharing && this.props.ifEnableGotoMeeting
            &&
            <div>
              <div className={style.screenSharing}>
                <div className={classnames(style.screenSharingTitle, style.testMsg)}>
                  GoToMeeting Integration
                </div>
                <span
                  className={classnames(style.loading, style.successMsg)}
                  style={{ display: this.props.isTesting ? 'inline-block' : 'none' }}
                >
                  <Icon
                    type={icons.loadingGreen}
                    className={iconStyle.g2mTestLoading}
                    size={14}
                  />
                </span>
                {
                  !this.props.isTesting
                  &&
                  <div className={style.testMsg}>
                    <span
                      className={style.successMsg}
                      style={{ display: this.props.ifTestG2ASuccess ? 'block' : 'none' }}
                    >
                      Successful connection test.
                    </span>
                    <span
                      className={style.errorMsg}
                      style={{ display: !this.props.ifTestG2ASuccess ? 'block' : 'none' }}
                    >
                      {this.props.errorMsg}
                    </span>
                  </div>
                }
                <div style={{ display: this.props.info.ifG2MConnected ? 'none' : 'block' }}>
                  <ul>
                    <li>
                      <span className={style.title}>Email:</span>
                      <Input
                        meta={{}}
                        type="text"
                        className={classnames(inputStyle.input, style.inputWidth)}
                        input={{
                          value: this.props.info.g2mEmail,
                          onChange: this.handleChangeG2MEmail,
                        }}
                      />
                    </li>
                    <li>
                      <span className={style.title}>Password:</span>
                      <Input
                        meta={{}}
                        type="password"
                        className={classnames(inputStyle.input, style.inputWidth)}
                        input={{
                          value: this.props.info.g2mPassword,
                          onChange: this.handleChangeG2MPassword,
                        }}
                      />
                      <span
                        className={style.linkBtn}
                        onClick={this.clickG2MConnection}
                      >
                        Test Connection
                      </span>
                    </li>
                  </ul>
                </div>
                <div style={{ display: this.props.info.ifG2MConnected ? 'block' : 'none' }}>
                  <ul>
                    <li>
                      <span className={style.title}>Email:</span>
                      <span style={{ color: '#888' }}>
                        {this.props.info.g2mEmail}
                      </span>
                      <span
                        className={style.linkBtn}
                        onClick={this.clickResetG2M}
                      >
                        Reset Account
                      </span>
                    </li>
                  </ul>
                </div>
              </div>
            </div>
          }
          {
            this.props.ifEnableScreenSharing
            && this.props.ifEnableJoinMe
            && (
              <div className={style.screenSharing}>
                <div className={style.screenSharingTitle}>Join.Me Integration</div>
                {this.state.checkingJoinMe && (<span className={style.joinMeInfo}>{'Loading...'}</span>)}
                {!this.state.checkingJoinMe && !this.state.ifJoinMeConnnected
                && (<span className={style.joinMeInfo}>{'No associated account was found.'}</span>)}
                {!this.state.checkingJoinMe && this.state.ifJoinMeConnnected
                && (
                  <div>
                    <span className={style.title}>Email:</span>
                    {this.state.joinMeAccount}
                    <span
                      className={style.linkBtn}
                      onClick={this.clickResetJoinMe}
                    >
                      Reset Account
                    </span>
                  </div>
                )}
              </div>
            )
          }
        </ul>
      </div>
    );
  }
}

export default OthersTab;

OthersTab.propTypes = {
  info: React.PropTypes.shape({
    ifAutoStartup: React.PropTypes.bool.isRequired,
    ifAutoLogin: React.PropTypes.bool.isRequired,
    ifExitCloseWindow: React.PropTypes.bool.isRequired,
    ifEnableAutoMonitor: React.PropTypes.bool.isRequired,
    ifMonitorChatPermission: React.PropTypes.bool.isRequired,
    autoCloseChatDelayMinutes: React.PropTypes.string.isRequired,
    autoCloseChatWindow: React.PropTypes.bool.isRequired,
    g2mEmail: React.PropTypes.string.isRequired,
    g2mPassword: React.PropTypes.string.isRequired,
    ifG2MConnected: React.PropTypes.bool.isRequired,
    joinMeIntegrationServer: React.PropTypes.string.isRequired,
    agentInfo: React.PropTypes.shape({}),
    ifAutoAway: React.PropTypes.bool.isRequired,
    autoAwayMinutes: React.PropTypes.string.isRequired,
    ifAutoLogoff: React.PropTypes.bool.isRequired,
    autoLogoffMinutes: React.PropTypes.string.isRequired,
  }),
  ifEnableScreenSharing: React.PropTypes.bool.isRequired,
  ifEnableGotoMeeting: React.PropTypes.bool.isRequired,
  ifEnableJoinMe: React.PropTypes.bool.isRequired,
  testG2MConnect: React.PropTypes.func.isRequired,
  updateOthersTab: React.PropTypes.func.isRequired,
  isTesting: React.PropTypes.bool.isRequired,
  errorMsg: React.PropTypes.string,
  ifTestG2ASuccess: React.PropTypes.bool,
  focusElement: React.PropTypes.string,
};



