import React from 'react';
// import classnames from 'classnames';
import style from './style.css';
import Transfer from '../../Transfer/Transfer';
import * as preferenceEnums from '../../../constants/enums/preference';
import { reverseSchema } from '../../../utils/schema';

const getCustomVariableName = (variableId, variables) => {
  const tmp = variables.filter(item => item.id === variableId);
  let name = '';
  if (tmp.length > 0) {
    name = tmp[0].name;
  }
  return name;
};

const enumColumnReverse = reverseSchema(preferenceEnums.enumColumn);

const columnToColumnName = (enumColumn, variableId, variables) => {
  let name = '';
  if (enumColumn === preferenceEnums.enumColumn.CustomVariable) {
    name = `${getCustomVariableName(variableId, variables)}
      (${preferenceEnums.enumColumnName.CustomVariable})`;
  } else {
    name = preferenceEnums.enumColumnName[enumColumnReverse[enumColumn]];
  }
  return name;
};

const filterValidColumns = (columns, variables) => {
  let i = 0;
  const validColumns = [];
  columns.forEach((item) => {
    if (item.ifVisible) {
      const displayName = columnToColumnName(item.enumColumn, item.customVariableId, variables);
      if (displayName && displayName !== '') {
        validColumns.push({
          id: i,
          name: displayName,
          data: item,
          ifSelected: item.ifVisible,
        });
        i += 1;
      }
    }
  });
  columns.forEach((item) => {
    if (!item.ifVisible) {
      const displayName = columnToColumnName(item.enumColumn, item.customVariableId, variables);
      if (displayName && displayName !== '') {
        validColumns.push({
          id: i,
          name: displayName,
          data: item,
          ifSelected: item.ifVisible,
        });
        i += 1;
      }
    }
  });
  return validColumns;
};

class ColumnsTab extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      // ifTestG2ASuccess: props.ifTestG2ASuccess,
    };
  }

  render() {
    return (
      <div className={style.settingsContent}>
        <div className={style.columnsTitle}>
          {'Select which columns to display in your Agent Console window.'}
        </div>
        <Transfer
          unselectedTitle={'Hide these columns'}
          selectedTitle={'Display these columns'}
          items={filterValidColumns(this.props.info,
            this.props.siteInfo.customVariables)}
          selected={filterValidColumns(this.props.info,
            this.props.siteInfo.customVariables).filter(item => item.ifSelected === true)}
          unselected={filterValidColumns(this.props.info,
            this.props.siteInfo.customVariables).filter(item => item.ifSelected !== true)}
          onChange={this.props.updateColumnsTab}
        />
      </div>
    );
  }
}

export default ColumnsTab;

ColumnsTab.propTypes = {
  info: React.PropTypes.arrayOf(React.PropTypes.shape({
    enumColumn: React.PropTypes.number.isRequired,
    ifVisible: React.PropTypes.bool.isRequired,
    width: React.PropTypes.number.isRequired,
    customVariableId: React.PropTypes.number.isRequired,
  })).isRequired,
  siteInfo: React.PropTypes.shape({
    ifTicketEnable: React.PropTypes.bool.isRequired,
    ifDepartmentEnable: React.PropTypes.bool.isRequired,
    ifMultipleCodePlan: React.PropTypes.bool.isRequired,
    ifCanCustomAutoInvitationRules: React.PropTypes.bool.isRequired,
    ifRecordNavigation: React.PropTypes.bool.isRequired,
    ifEnableSegmentation: React.PropTypes.bool.isRequired,
    customVariables: React.PropTypes.arrayOf(React.PropTypes.shape({
      id: React.PropTypes.number.isRequired,
      name: React.PropTypes.string.isRequired,
      type: React.PropTypes.number.isRequired,
    })),
  }),
  updateColumnsTab: React.PropTypes.func.isRequired,
};



