import React from 'react';
import classnames from 'classnames';
import style from './transfer.css';
import { icons } from '../../constants/enums';
import Icon from '../Icon/Icon';
// import iconStyle from '../Icons/Icon.css';

const swap = (array, first, second) => {
  const final = array.slice();
  const temp = final[first];
  final[first] = final[second];
  final[second] = temp;
  return final;
};

class Transfer extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      selected: props.items.filter(item => item.ifSelected === true),
      unselected: props.items.filter(item => item.ifSelected === false),
      selectedIndex: -1,
      unselectedIndex: -1,
    };

    this.selectAll = this.selectAll.bind(this);
    this.unselectAll = this.unselectAll.bind(this);
    this.selectOptionClick = this.selectOptionClick.bind(this);
    this.unSelectOptionClick = this.unSelectOptionClick.bind(this);
    this.selectDoubleClick = this.selectDoubleClick.bind(this);
    this.selectOne = this.selectOne.bind(this);
    this.unselectOne = this.unselectOne.bind(this);
    this.moveToFirst = this.moveToFirst.bind(this);
    this.moveUp = this.moveUp.bind(this);
    this.moveDown = this.moveDown.bind(this);
    this.moveToLast = this.moveToLast.bind(this);
    this.updateValue = this.updateValue.bind(this);
  }

  selectOptionClick(index) {
    this.setState({
      selectedIndex: index,
    });
  }

  unSelectOptionClick(index) {
    this.setState({
      unselectedIndex: index,
    });
  }

  selectAll() {
    const selectedItem = [];
    this.props.selected.forEach((item) => {
      selectedItem.push(Object.assign(item.data));
    });
    this.props.unselected.forEach((item) => {
      selectedItem.push(Object.assign(item.data, { ifVisible: true }));
    });
    this.setState({
      unselectedIndex: -1,
      selectedIndex: -1,
    }, this.props.onChange(selectedItem));
  }

  unselectAll() {
    const selectedItem = [];
    this.props.unselected.forEach((item) => {
      selectedItem.push(Object.assign(item.data));
    });
    this.props.selected.forEach((item) => {
      selectedItem.push(Object.assign(item.data, { ifVisible: false }));
    });
    this.setState({
      unselectedIndex: -1,
      selectedIndex: -1,
    }, this.props.onChange(selectedItem));
  }

  selectOne() {
    if (this.state.unselectedIndex < 0) {
      return;
    }
    const selectedIndex = this.state.unselectedIndex;
    const allItems = [];
    this.props.selected.forEach((item) => {
      allItems.push(item.data);
    });
    allItems.push(Object.assign(this.props.items[selectedIndex].data, { ifVisible: true }));
    this.props.unselected.forEach((item, index) => {
      if (index !== selectedIndex - this.props.selected.length) {
        allItems.push(item.data);
      }
    });
    this.setState({
      unselectedIndex: -1,
    }, this.props.onChange(allItems));
  }

  unselectOne() {
    if (this.state.selectedIndex < 0) {
      return;
    }
    const selectedIndex = this.state.selectedIndex;
    const allItems = [];
    this.props.items.forEach((item, index) => {
      if (index !== selectedIndex) {
        allItems.push(item.data);
      }
    });
    allItems.push(Object.assign(this.props.items[selectedIndex].data, { ifVisible: false }));
    this.setState({
      selectedIndex: -1,
    }, this.props.onChange(allItems));
  }

  unSelectDoubleClick(index) {
    this.setState({
      selectedIndex: -1,
      unselectedIndex: index,
    });
    this.selectOne();
  }

  selectDoubleClick(index) {
    this.setState({
      selectedIndex: index,
      unselectedIndex: -1,
    });
    this.unselectOne();
  }

  moveToFirst() {
    if (this.state.selectedIndex <= 0) {
      return;
    }
    const selectedIndex = this.state.selectedIndex;
    const allItems = [];
    allItems.push(this.props.items[selectedIndex].data);
    this.props.items.forEach((item, index) => {
      if (index !== selectedIndex) {
        allItems.push(item.data);
      }
    });
    this.setState({
      selectedIndex: 0,
    }, this.props.onChange(allItems));
  }

  moveUp() {
    if (this.state.selectedIndex < 0) {
      return;
    }
    const selectedIndex = this.state.selectedIndex;
    const newItems = swap(this.props.items, this.state.selectedIndex - 1, this.state.selectedIndex);
    const allItems = [];
    newItems.forEach((item) => {
      allItems.push(item.data);
    });
    this.setState({
      selectedIndex: selectedIndex - 1,
    }, this.props.onChange(allItems));
  }

  moveDown() {
    if (this.state.selectedIndex < 0
      || this.state.selectedIndex >= this.props.selected.length - 1) {
      return;
    }
    const selectedIndex = this.state.selectedIndex;
    const newItems = swap(this.props.items, this.state.selectedIndex, this.state.selectedIndex + 1);
    const allItems = [];
    newItems.forEach((item) => {
      allItems.push(item.data);
    });
    this.setState({
      selectedIndex: selectedIndex + 1,
    }, this.props.onChange(allItems));
  }

  moveToLast() {
    if (this.state.selectedIndex < 0
      || this.state.selectedIndex === this.props.selected.length - 1) {
      return;
    }
    const selectedIndex = this.state.selectedIndex;
    const targetIndex = this.props.selected.length - 1;
    const allItems = [];
    this.props.selected.forEach((item, index) => {
      if (index !== selectedIndex) {
        allItems.push(item.data);
      }
    });
    allItems.push(this.props.items[selectedIndex].data);
    this.props.unselected.forEach((item) => {
      allItems.push(item.data);
    });
    this.setState({
      selectedIndex: targetIndex,
    }, this.props.onChange(allItems));
  }

  updateValue() {
    this.props.onChange(this.state.selected.concat(this.state.unselected));
  }

  render() {
    return (
      <div>
        <div
          className={classnames(style.halfWidth)}
        >
          <div className={classnames(style.list, style.col)}>
            <div className={style.header}>
              {this.props.unselectedTitle}
            </div>
            <div className={style.optionsWrapper}>
              {
                this.props.unselected.map((option, index) =>
                    (<div
                      key={`option_${index}`}
                      className={classnames(style.option,
                        this.state.unselectedIndex === option.id && style.selected)}
                      onClick={() => this.unSelectOptionClick(option.id)}
                      onDoubleClick={() => this.unSelectDoubleClick(option.id)}
                    >
                      {option.name}
                    </div>)
                  )
              }
            </div>
          </div>
          <div className={classnames(style.operations, style.col)}>
            <div className={style.operation}>
              <Icon
                type={icons.transferSelectAll}
                size={20}
                className={style.icons}
                onClick={this.selectAll}
              />
            </div>
            <div className={style.operation}>
              <Icon
                type={icons.transferSelect}
                size={20}
                className={style.icons}
                onClick={this.selectOne}
              />
            </div>
            <div className={style.operation}>
              <Icon
                type={icons.transferUnselect}
                size={20}
                className={style.icons}
                onClick={this.unselectOne}
              />
            </div>
            <div className={style.operation}>
              <Icon
                type={icons.transferUnselectAll}
                size={20}
                className={style.icons}
                onClick={this.unselectAll}
              />
            </div>
          </div>
        </div>
        <div
          className={classnames(style.halfWidth)}
        >
          <div className={classnames(style.list, style.col)}>
            <div className={style.header}>
              {this.props.selectedTitle}
            </div>
            <div className={style.optionsWrapper}>
              {
                this.props.selected.map((option, index) =>
                  (<div
                    key={`option_${index}`}
                    className={classnames(style.option,
                      this.state.selectedIndex === option.id && style.selected)}
                    onClick={() => this.selectOptionClick(option.id)}
                    onDoubleClick={() => this.selectDoubleClick(option.id)}
                  >
                    {option.name}
                  </div>)
                )
              }
            </div>
          </div>
          <div className={classnames(style.operations, style.col)}>
            <div className={style.operation}>
              <Icon
                type={icons.transferUpFirst}
                size={20}
                className={style.icons}
                onClick={this.moveToFirst}
              />
            </div>
            <div className={style.operation}>
              <Icon
                type={icons.transferUp}
                size={20}
                className={style.icons}
                onClick={this.moveUp}
              />
            </div>
            <div className={style.operation}>
              <Icon
                type={icons.transferDown}
                size={20}
                className={style.icons}
                onClick={this.moveDown}
              />
            </div>
            <div className={style.operation}>
              <Icon
                type={icons.transferDownLast}
                size={20}
                className={style.icons}
                onClick={this.moveToLast}
              />
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Transfer;

Transfer.propTypes = {
  unselectedTitle: React.PropTypes.string.isRequired,
  selectedTitle: React.PropTypes.string.isRequired,
  items: React.PropTypes.arrayOf(React.PropTypes.shape()).isRequired,
  selected: React.PropTypes.arrayOf(React.PropTypes.shape()).isRequired,
  unselected: React.PropTypes.arrayOf(React.PropTypes.shape()).isRequired,
  onChange: React.PropTypes.func,
};



