import React from 'react';
import classnames from 'classnames';
import * as language from 'src/constants/enums/language';
import Select from '../../Select/Select';
import style from './style.css';
import Checkbox from '../../Checkbox/Checkbox';
import * as preferenceEnums from '../../../constants/enums/preference';
import ModalStyle from '../Modal.css';

const translateLanguages = () =>
  preferenceEnums.translateLanguageNames.map((item, index) => (
    {
      text: item,
      value: index + 1,
    }
  ));

const TextDirectionSelect = [
  { text: 'Left to Right', value: 0 },
  { text: 'Right to Left', value: 1 },
];

const SpellLanguageSelect = [
  { text: 'English', value: language.english },
  { text: 'Spanish', value: language.spanish },
  { text: 'French', value: language.french },
  { text: 'German', value: language.german },
];

class LanguageTab extends React.Component {
  constructor(props) {
    super(props);
    this.handleChangeTextDiretion = this.handleChangeTextDiretion.bind(this);
    this.handleEnableSpellCheck = this.handleEnableSpellCheck.bind(this);
    this.handleChangeLanguageSpellCheck = this.handleChangeLanguageSpellCheck.bind(this);
    this.handleEnableTranslation = this.handleEnableTranslation.bind(this);
    this.handleChangeTranslateToLanguage = this.handleChangeTranslateToLanguage.bind(this);
  }

  handleChangeTextDiretion(value) {
    const newInfo = Object.assign({}, this.props.info, {
      textDirectionIfRTL: value === 1,
    });
    this.props.updateLanguageTab(newInfo);
  }

  handleEnableSpellCheck(value) {
    const newInfo = Object.assign({}, this.props.info, {
      ifEnableSpellCheck: value,
    });
    this.props.updateLanguageTab(newInfo);
  }

  handleChangeLanguageSpellCheck(value) {
    const newInfo = Object.assign({}, this.props.info, {
      languageSpellCheck: value,
    });
    this.props.updateLanguageTab(newInfo);
  }

  handleEnableTranslation(value) {
    const newInfo = Object.assign({}, this.props.info, {
      ifEnableTranslate: value,
    });
    this.props.updateLanguageTab(newInfo);
  }

  handleChangeTranslateToLanguage(value) {
    const newInfo = Object.assign({}, this.props.info, {
      translateLanguage: value,
    });
    this.props.updateLanguageTab(newInfo);
  }

  render() {
    return (
      <div className={style.settingsContent}>
        <div className={style.configCols}>
          <ul>
            <li>
              <div>Text Direction</div>
            </li>
            <li>
              <div>
                <Checkbox
                  input={{
                    value: this.props.info.ifEnableSpellCheck,
                    onChange: this.handleEnableSpellCheck }}
                  text="Enable Spell Check"
                />
              </div>
            </li>
            {
              this.props.ifEnableChatIntegration && this.props.ifEnableChatTranslation
              &&
              (
                <div>
                  <Checkbox
                    input={{
                      value: this.props.info.ifEnableTranslate,
                      onChange: this.handleEnableTranslation }}
                    text="Enable Auto Chat Translation to"
                  />
                </div>
              )
            }
          </ul>
        </div>
        <div className={classnames(style.configCols, style.rightCols)}>
          <ul>
            <li>
              <div>
                <Select
                  input={{
                    value: this.props.info.textDirectionIfRTL ? 1 : 0,
                    onChange: this.handleChangeTextDiretion,
                  }}
                  options={TextDirectionSelect}
                  width={200}
                  dropdownClassName={ModalStyle.dropdown}
                />
              </div>
            </li>
            <li>
              <div>
                <div>
                  <Select
                    input={{
                      value: this.props.info.languageSpellCheck,
                      onChange: this.handleChangeLanguageSpellCheck,
                    }}
                    options={SpellLanguageSelect}
                    width={200}
                    dropdownClassName={ModalStyle.dropdown}
                  />
                </div>
              </div>
            </li>
            {
              this.props.ifEnableChatIntegration && this.props.ifEnableChatTranslation
              &&
              (
                <li>
                  <div>
                    <Select
                      input={{
                        value: this.props.info.translateLanguage,
                        onChange: this.handleChangeTranslateToLanguage,
                      }}
                      options={translateLanguages()}
                      width={200}
                      dropdownClassName={ModalStyle.dropdown}
                    />
                  </div>
                </li>
              )
            }
          </ul>
        </div>
      </div>
    );
  }
}

export default LanguageTab;

LanguageTab.propTypes = {
  info: React.PropTypes.shape({
    textDirectionIfRTL: React.PropTypes.bool.isRequired,
    ifEnableSpellCheck: React.PropTypes.bool.isRequired,
    languageSpellCheck: React.PropTypes.number.isRequired,
    ifEnableTranslate: React.PropTypes.bool.isRequired,
    translateLanguage: React.PropTypes.number.isRequired,
  }),
  ifEnableChatIntegration: React.PropTypes.bool.isRequired,
  ifEnableChatTranslation: React.PropTypes.bool.isRequired,
  updateLanguageTab: React.PropTypes.func.isRequired,
};



