import React from 'react';
import classnames from 'classnames';
import Icon from 'src/components/Icon/Icon';
import style from './style.css';

const MenuTab = ({
  tabs,
  selectedTab,
  onSelect,
  onIconClick,
  ifEqualWidth = true,
  className,
}) => (
  <ul className={classnames(style.tab, className)}>
    {
      tabs.map(tab => (
        <li
          style={
            ifEqualWidth ? { width: tabs.length > 0 ? `${100 / tabs.length}%` : '100%' } :
            {
              width: tab.width ? tab.width : 'auto',
              paddingLeft: tab.icon ? 15 : 0,
              paddingRight: tab.icon ? 15 : 0,
            }
          }
          key={tab.type}
          className={tab.type === selectedTab && style.selected}
          onClick={tab.type !== selectedTab && (() => { onSelect(tab.type); })}
        >
          {tab.name}
          {tab.icon && (
            <Icon
              type={tab.icon}
              size={9}
              onClick={() => onIconClick(tab.type)}
              style={{ paddingLeft: 10, cursor: 'pointer' }}
            />
          )}
        </li>
      ))
    }
  </ul>
);

MenuTab.propTypes = {
  tabs: React.PropTypes.arrayOf(React.PropTypes.shape({
    name: React.PropTypes.string.isRequired,
    type: React.PropTypes.number.isRequired,
    icon: React.PropTypes.string,
  })).isRequired,
  selectedTab: React.PropTypes.number.isRequired,
  onSelect: React.PropTypes.func.isRequired,
  onIconClick: React.PropTypes.func,
  ifEqualWidth: React.PropTypes.bool,
  className: React.PropTypes.string,
};

export default MenuTab;



