import React from 'react';
import classnames from 'classnames';
import style from './style.css';
import Input from '../../Input/Input';
import inputStyle from '../../Input/Input.css';
import * as preferenceEnums from '../../../constants/enums/preference';


function getKeyName(keycode) {
  let name = '';
  if (preferenceEnums.KeycodeDic[keycode]) {
    name = preferenceEnums.KeycodeDic[keycode];
  } else {
    name = '';
  }
  return name;
}

const isValidActionKey = (keycode) => {
  if (keycode === 17 || keycode === 16 || keycode === 18) {
    return 0;
  }
  return keycode;
};

const getKeyNames = (keysInfo) => {
  const names = [];
  names.push(getKeyName(17));
  if (keysInfo.shiftKey) {
    names.push(getKeyName(16));
  }
  if (keysInfo.altKey) {
    names.push(getKeyName(18));
  }
  names.push(getKeyName(keysInfo.actionKey));
  return names.join(' + ');
};

function init(keycodeArray) {
  const keysInfo = {
    commandKey: false,
    controlKey: true,
    altKey: false,  // mac option key
    shiftKey: false,
    actionKey: 0,
  };
  keycodeArray.forEach((key) => {
    if (key === 17) {
      keysInfo.controlKey = true;
    } else if (key === 16) {
      keysInfo.shiftKey = true;
    } else if (key === 18) {
      keysInfo.altKey = true;
    } else {
      keysInfo.actionKey = isValidActionKey(key);
    }
  });
  return keysInfo;
}

function getKeycodeArray(pressedKeys) {
  const keys = [];
  keys.push(17);  // always with Ctrl Pressed
  if (pressedKeys.shiftKey) {
    keys.push(16);
  }
  if (pressedKeys.altKey) {
    keys.push(18);
  }
  keys.push(isValidActionKey(pressedKeys.actionKey));
  return keys;
}

class Shortcut extends React.Component {
  constructor(props) {
    super(props);
    this.state = init(props.keys);
    this.handleKeypress = this.handleKeypress.bind(this);
    this.handleChange = this.handleChange.bind(this);
  }

  handleKeypress(event) {
    let keyPressInfo = {};
    event.target.addEventListener('keydown', (e) => {
      e.preventDefault();
      keyPressInfo = {
        controlKey: e.ctrlKey,
        altKey: e.altKey,
        shiftKey: e.shiftKey,
        actionKey: isValidActionKey(e.keyCode), // Avoid duplications of ctrl/alt/shift keys
        commandKey: false, // no use
      };
      this.setState(Object.assign({}, keyPressInfo), () => {
        this.props.handleChange(getKeycodeArray(this.state));
      });
    }, false);
  }

  handleChange() {
    this.props.handleChange(getKeycodeArray(this.state));
  }

  render() {
    return (
      <Input
        meta={{}}
        type="text"
        className={classnames(inputStyle.input, style.inputWidth)}
        input={{
          value: getKeyNames(this.state),
          onFocus: this.handleKeypress,
          onChange: this.props.handleChange,
        }}
      />
    );
  }
}

export default Shortcut;

Shortcut.propTypes = {
  keys: React.PropTypes.arrayOf(React.PropTypes.number.isRequired).isRequired,
  handleChange: React.PropTypes.func.isRequired,
};



